import React from 'react'
import './SecondPages.scss'
const SecondPage = () => {
    return (
        <div className="second">
            <div className="second-container">
                <h2 className="second-container-title">ABOUT ME</h2>
                <p className="second-container-text">
                    I'M 21 YEARS OLD, REACT DEVELOPER. I STARTED STUDYING REACT
                    IN NOVEMBER 2021, BUT IT THIS BEEN MY DREAM TO BECOME A WEB
                    DEVELOPER AND I LOVE MY WORK.
                </p>
            </div>
        </div>
    )
}

export default SecondPage
